

<template>
    <div class="cinema">
        <!-- 头部 -->
        <Movie-header :title="'影院'" :type="'cinema'"></Movie-header>
        <!-- 影院 - 主体 -->
        <main id="main">
            <!-- row -->
            <div class="cinema-row" v-for="i in cinema.cinemas" :key="i.id">
                <!-- title -->
                <h3>{{i.nm}}<span> {{i.sellPrice}}元起</span></h3>
                <!-- 位置 -->
                <section class="cinema-row-location box-flex">
                    <div>{{i.addr}}</div>
                    <div>{{i.distance}}</div>
                </section>
                <!-- 标签 -->
                <section class="cinema-row-lable box-flex">
                    <div>改签</div>
                    <div>小吃</div>
                    <div>折扣卡</div>
                    <div>杜比全景声厅</div>
                </section>
                <!-- 活动 -->
                <div class="cinema-row-activity">
                    <div class="box-flex">
                        <img src="@/assets/hui.png" height="15" />
                        <div class="activity-content">{{i.promotion.cardPromotionTag}}</div>
                    </div>
                    <div class="box-flex">
                        <img src="@/assets/hui.png" height="15" />
                        <div class="activity-content">{{i.promotion.platformActivityTag}}</div>
                    </div>
                </div>
            </div>
        </main>
        <!-- 脚注 - 导航 -->
        <Movie-footer></Movie-footer>
    </div>
</template>


<script>
import header from "@/components/Movie-header";
import footer from "@/components/Movie-footer";

export default {
    name: "cinema",
    components: {
        "Movie-header": header,
        "Movie-footer": footer,
    },
    data() {
      return {
        cinema: []
      }
    },
    methods: {

        // 获取影院数据
        OPEN_CinemaData() {
            this.axios
                .get('/server/cinema/cinemaList.json')
                .then(res => {
                    this.cinema = res.data
                    console.log(this.cinema)
                })
                .catch(err => { consloe.log(err) })
        }

    },
    mounted() {
        // 获取影院数据
        this.OPEN_CinemaData()
    },
};
</script>

