    border-bottom: 1px solid #f5f5f5;
    box-sizing: border-box;
}
.cinema-row > h3 {
    font-weight: 100;
    font-size: 1rem;
    font-family: "黑体";
}
.cinema-row > h3 > span {
    color: #f03d37;
}
/* 位置 */
.cinema-row-location {
    font-size: 0.8rem;
}
.cinema-row-location > div:nth-child(2) {
    margin-left: 15px;
    color: #666;
}
/* 标签 */
.cinema-row-lable > div {
    position: relative;
    display: inline-block;
    padding: 0 3px;
    height: 15px;
    line-height: 15px;
    border-radius: 2px;
    font-size: 0.6rem;
    color: #589daf;
