}
/* 活动 */
.cinema-row-activity {
    margin-top: 0.5rem;
}
.cinema-row-activity > div {
    /* margin-top: .5rem; */
}
.activity-content {
    font-size: 0.7rem;
    transform: translate(0, -0.3rem);
    margin-left: 5px;
    color: #999;
    letter-spacing: 1px;
}
</style>

